import errorCode from "@/utils/errorCode";
import {
	config
} from '@/config.js'

// request
export const apiResquest = (params) => {
  var url = config.base_url + params.url;
  // GET
  if (!params.method) {
    params.method = "GET";
  }
  return new Promise((resolve, reject) => {
    uni.showLoading({
        title: '',
        mask: true
    })
    return uni.request({
      url: url,
      data: params.data,
      header: {
        "content-type": "application/json",
        token: wx.getStorageSync("token"), // token
      },
      method: params.method,
      success: function (res) {
        uni.hideLoading()
        // 
        const code = res.data.code || 200;
        // 
        const message = errorCode[code] || res.data.msg || errorCode["default"];
        // token
        if (code === 401) {
          uni.showModal({
              title: '',
              content: '',
              success(res) {
                  //
                  if (res.confirm) {
                      uni.getUserProfile({
                          desc: '',
                          success: res => {
                              console.log(res);
                              console.log(1);
                          },
                          fail: res => {
                              console.log(2);
                              console.log(res)
                              //
                              uni.showToast({
                                  title: ',',
                                  icon: 'error',
                                  duration: 2000
                              });
                              return;
                          }
                      });
                  } else if (res.cancel) {
                      //
                      console.log(3);
                      uni.showToast({
                          title: ',',
                          icon: 'error',
                          duration: 2000
                      });
                      return;
                  }
              }
          });        
        } else if (code === 500) {
          wx.showToast({
              title: "o()o",
              icon: "none",
          });
          return;
        } else if (code !== 200) {
          wx.showToast({
              title: message,
              icon: "none",
          });
          return;
        }
        // 
        resolve(res.data);
      },
      fail: (err) => {
				reject(err);
				uni.hideLoading()
			},
			complete: () => {
				uni.hideLoading()
			}
    });
  })
  
}
